@import url('https://fonts.googleapis.com/css?family=Muli');

/*reset*/
* {
  margin: 0;
  padding: 0;
}

/*product info */

h1 {
  color: #ff6d39;
  font-family: "muli";
  font-weight: bold;
  font-size: 45px;
  margin-top: 21px;
  display: inline-block;
}

i.fa.fa-search {
  margin-left: 90px;
}

.product-name i {
  color: #ffffff;
  transition: 0.3s all ease;
  margin: 0px 12px;
}

.product-name i:hover {
  color: #ff6d39;
  cursor: pointer;
}

h3 {
  color: #ffffff;
    font-family: "muli";
    margin-top: 81px;
    font-size: 20px;
    font-weight: 500;
}

h2 {
  color: #ffffff;
  font-family: "muli";
  margin-top: 10px;
  font-weight: 800;
  font-size: 29px;
}

h4 {
  display: inline-block;
  color: #ffffff;
  font-family: "muli";
  margin-top: 10px;
  font-weight: bold;
  font-size: 20px;
}
h5 {
  display: inline-block;
  color: #ffffff;
  font-family: "muli";
  margin-top: 10px;
  font-weight: bold;
  /* font-size: 20px; */
}

h4.dis {
  display: inline-block;
  color: #ffffff;
  font-family: "muli";
  font-weight: 400;
  font-size: 17px;
  margin-left: 30px;
  text-decoration: line-through #ea3201;
}

h4.dis span {
  text-decoration: line-through #ea3201;
}

.discount {
  display: inline-block;
}

ul {
  list-style-type: none;
}

.buynow-ul-tags{
    margin-top: 25px;
    width: 100%;
}

li {
  display: inline-block;
  margin-right: 25px;
}

ul li {
  color: #ffffff;
  font-family: "muli";
  margin-top: 20px;
  font-weight: 500;
  font-size: 11px;
}

.bg {
  width: 15px;
  height: 15px;
  text-align: center;
  padding: 2px;
  margin-right: 20px;
  transition: 0.3s all ease;
  border-radius: 50%;
}

.bg:hover {
  background-color: #ff6d39;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  cursor: pointer;
}

.yellow {
  content: "";
  width: 13px;
  height: 13px;
  background-color: #fec60f;
  border-radius: 50%;
  border: 2px solid rgba(0, 0, 0, 0);
  transition: 0.3s all ease;
}

.black {
  content: "";
  width: 13px;
  height: 13px;
  background-color: #000000;
  border-radius: 50%;
  border: 2px solid rgba(0, 0, 0, 0);
  transition: 0.3s all ease;
}

.blue {
  content: "";
  width: 13px;
  height: 13px;
  background-color: #02a2ca;
  border-radius: 50%;
  border: 2px solid rgba(0, 0, 0, 0);
  transition: 0.3s all ease;
}

.yellow:hover,
.black:hover,
.blue:hover {
  border: 2px solid #f76b39;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  cursor: pointer;
}

.foot {
    color: #ffffff;
    font-family: "muli";
    margin-top: 30px;
    margin-right: 50px;
    font-weight: 500;
    padding-left: 39px;
    font-size: 44px;
    padding-top: 40px;
    float: left;
    transition: 0.3s all ease;
}

.foot i:nth-child(1) {
  margin-left: 0;
  margin-right: 15px;
}

.foot:hover {
  color: #f76b39;
  cursor: pointer;
}

/*shoe slider indicator*/

.left i {
  color: #ffd5c6;
  margin-top: 260px;
  transition: 0.3s all ease;
}

.fa-long-arrow-left {
  margin-left: -275px;
}

.fa-long-arrow-right {
  margin-left: 15px;
}

.left i:hover {
  cursor: pointer;
  color: #2a2f40;
}

/*main card*/

.card {
    display: flex;
    /* align-items: center; */
    background: #252831 url(https://www.dropbox.com/s/fuadz3vmw2nsylm/bg.png?raw=1) no-repeat;
    height: 86vh;
    width: 100%;
    margin: 0 auto;
    box-shadow: 0px 15px 50px 10px rgba(0, 0, 0, 0.4);
    margin-top: 0%;
}

.left {
    content: "";
    height: 570px;
    width: 562px;
    display: flex;
    align-items: center;
    background-color: #ff6d39;
    /* margin-left: 249px; */
    left: 0;
    border-radius: 0% 50% 50% 0%;
    position: absolute;
    z-index: 5;
}

.left img {
    height: 330px;
    margin-left: 23px;
    margin-top: -10px;
    width: 450px;
}

.right {
  content: "";
  height: 85%;
    width: 100%;
  background-color: #2a2f40;
  z-index: 3;
  /* margin-left: 200px; */
  right: 50px;
}

.product-info {
  position: absolute;
  /* margin-left: 672px; */
  height: 80%;
  right: 50px;
  /* width: 305px; */
  z-index: 10;
  /* width: 50%; */
}


.buynow-button-plus{
    margin-left: 26px;
    width: 50px;
    height: 30px;
    font-size: 20px;
}
.buynow-button-minus{
    margin-left: 10px;
    width: 50px;
    height: 30px;
    font-size: 20px;
}

.buynow-count{
    margin-top: 30px;
}